h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6 {
  margin-top: 0;
  margin-bottom: text-spacing(s);
  font-weight: bold;
  color: inherit;
}

h1,
.h1 {
  @include font-size(h1);
}

h2,
.h2 {
  @include font-size(h2);
}

h3,
.h3 {
  @include font-size(h3);
}

h4,
.h4 {
  @include font-size(h4);
}

h5,
.h5 {
  @include font-size(h5);
}

h6,
.h6 {
  @include font-size(h6);
}
